import {
  Box,
  Button,
  Drawer,
  DrawerBody,
  DrawerContent,
  DrawerHeader,
  DrawerOverlay,
  Flex,
  Heading,
  Image,
  Text,
  useDisclosure,
} from "@chakra-ui/react";
import React, { useState } from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { animateScroll as scroll } from "react-scroll";
import hamburger from "../Images/hamburger-menu-svgrepo-com.svg";

export const Navbar = () => {
  const { isOpen, onToggle, onClose } = useDisclosure();
  const [placement, setPlacement] = useState("top");

  const scrollTo = (elementId, section) => {
    scroll.scrollTo(document.getElementById(elementId).offsetTop - 60, {
      duration: 1000,
      smooth: "easeInOutQuart",
    });
    onClose();
  };

  return (
    <Box bgColor={"white"}>
      <DIV>
        <Box
          display={{ base: "none", md: "inline" }}
          className="pblogo"
          id="nav-menu"
        >
          <Heading
            fontFamily={"sans-serif"}
            bgGradient="linear(to-r, #0a194e, #97a7e1)"
            bgClip="text"
            onClick={() => scrollTo("home")}
            cursor={"pointer"}
            display={{ base: "none", md: "inline" }}
          >
            [ Pavan ]
          </Heading>
        </Box>

        {/* Drawer */}

        <Box w={{ base: "20%", md: "0" }}>
          {" "}
          <Image
            src={hamburger}
            w={"50%"}
            colorScheme="blue"
            onClick={onToggle}
            display={"block"}
            m={"auto"}
          />
        </Box>

        <Drawer placement={placement} onClose={onClose} isOpen={isOpen}>
          <DrawerOverlay />
          <DrawerContent>
            <DrawerHeader
              borderBottomWidth="1px"
              textAlign={"center"}
              onClick={onClose}
              color={"red"}
            >
              Close
            </DrawerHeader>
            <DrawerBody textAlign={"center"}>
              <Flex direction={"column"}>
                <Link
                  className="nav-link home"
                  onClick={() => scrollTo("home")}
                >
                  Home
                </Link>
                <Link
                  className="nav-link about"
                  onClick={() => scrollTo("about")}
                >
                  About
                </Link>
                <Link
                  className="nav-link skills"
                  onClick={() => scrollTo("skills")}
                >
                  Skills
                </Link>
                <Link
                  className="nav-link projects"
                  onClick={() => scrollTo("projects")}
                >
                  Projects
                </Link>
                <Link
                  className="nav-link contact"
                  onClick={() => scrollTo("contact")}
                >
                  Contact
                </Link>
              </Flex>
            </DrawerBody>
          </DrawerContent>
        </Drawer>

        {/* Drawer */}

        <Flex
          display={{ base: "none", md: "flex" }}
          w={"40%"}
          justifyContent={"space-around"}
        >
          <Link className="nav-link home" onClick={() => scrollTo("home")}>
            Home
          </Link>
          <Link className="nav-link about" onClick={() => scrollTo("about")}>
            About
          </Link>
          <Link className="nav-link skills" onClick={() => scrollTo("skills")}>
            Skills
          </Link>
          <Link
            className="nav-link projects"
            onClick={() => scrollTo("projects")}
          >
            Projects
          </Link>
          <Link
            className="nav-link contact"
            onClick={() => scrollTo("contact")}
          >
            Contact
          </Link>
        </Flex>

        <Button bgColor={"#0a194e"} color={"white"} mb={3} mr={3}>
          Resume
        </Button>
      </DIV>
    </Box>
  );
};

const DIV = styled.div`
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-items: center;
  gap: 10px;
  /* box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px; */
  /* box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px; */
  padding-top: 20px;
  position: fixed;
  background-color: white;
  z-index: 999;

  @media (max-width: 790px) {
    justify-content: space-between;
  }
`;
